.box_wrap {
  position: absolute;
  width: 300px;
  height: 300px;
}

.box_wrap div {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  transform-style: preserve-3d;
  transform: rotateX(-20deg) rotateY(25deg);
  animation: animate 6s linear infinite;
}
@keyframes animate {
  0%{
    transform: rotateX(-20deg) rotateY(0deg);
  }
  100%{
    transform: rotateX(-20deg) rotateY(360deg);
  }
}
.box_wrap .box span {
  position: absolute;
  width: 300px;
  height: 300px;
  transform-style: preserve-3d;
  transition: 0.5s;
}
.box_wrap:hover .box span{
  background: transparent !important;
  border: 5px solid #ffa20d;
  filter:drop-shadow(0 0 20px #ffa20d);
}

.box_wrap .box span:nth-child(1) {
  transform: rotateY(0) translateZ(150px);
  background: #e2e2e2;
}

.box_wrap .box span:nth-child(2) {
  transform: rotateY(90deg) translateZ(150px);
  background: #f2f2f2;
}

.box_wrap .box span:nth-child(3) {
  transform: rotateY(180deg) translateZ(150px);
  background: #e2e2e2;
}

.box_wrap .box span:nth-child(4) {
  transform: rotateY(270deg) translateZ(150px);
  background: #f2f2f2;
}

.box_wrap .box span.top{
  transform: rotateX(90deg) translateZ(150px);
  background: #fff;
}

.box_wrap .box span.top::before{
  content: '';
  position: absolute;
  inset:0;
  transform: translateZ(-350px);
  background: rgba(255,255,255,0.5);
  filter:blur(50px);
}