:root {
  --font_color: #fff;
  --bg_color: #222;
}

@keyframes colorful {
  0% {
    color: #fff;
    filter: blur(1px) hue-rotate(0deg);
    text-shadow: 0 0 5px #00b3ff, 0 0 10px #00b3ff, 0 0 15px #00b3ff,
      0 0 20px #00b3ff, 0 0 25px #00b3ff, 0 0 30px #00b3ff;
  }

  100% {
    color: #fff;
    filter: blur(1px) hue-rotate(360deg);
    text-shadow: 0 0 5px #00b3ff, 0 0 10px #00b3ff, 0 0 15px #00b3ff,
      0 0 20px #00b3ff;
  }
}

/* layout */
* {
  margin: 0;
  padding: 0;
}

body {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  font-size: 1.5rem;
  background-color: var(--bg_color);
  color: var(--font_color);
  box-sizing: border-box;
}

li {
  list-style: none;
}

a {
  color: var(--font_color);
  text-decoration: none;
}

.inner {
  width: 100%;
  max-width: 1600px;
  margin: 0 auto;
}

.clearfix:after {
  content: "";
  display: block;
  clear: both;
}

/* header */
#header {
  position: fixed;
  width: 100%;
  height: 90px;
  border-bottom: 1px solid rgba(100, 100, 100, 0.5);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

#header span {
  padding: 0 20px;
  animation: colorful 4s linear infinite;
}

#header span a {
  font-size: 2rem;
}

#header .menu li {
  float: left;
  width: 125px;
  padding: 8px;
  text-align: center;
  font-weight: bold;
  position: relative;
  overflow: hidden;
}

#header .menu li:before {
  content: "";
  display: none;
  background: pink;
  width: 40px;
  height: 350%;
  position: absolute;
  left: calc(50% - 20px);
  top: calc(-50% - 40px);
  animation: spin 1.5s linear infinite;
}
#header .menu li:hover:before {
  display: block;
  z-index: -1;
}
#header .menu li:after {
  content: "";
  display: none;
  position: absolute;
  background: #222;
  inset: 3px;
}
#header .menu li:hover:after {
  display: block;
  z-index: -1;
}
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

/* section 공통 */
section {
  padding-top: 90px;
}

/* ./components/menu/Home */
#Home {
  width: 100%;
  height: 85vh;
}

#Home .inner {
  position: relative;
  width: 1280px;
  height: 100%;
}

#Home .inner > div {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-wrap: wrap;
  position: relative;
  top: calc(50% - 25vh);
}

#Home div .sparkling {
  position: relative;
  height: 80%;
  width: 50%;
  left: 10%;
  top: 50px;
  /* display: flex;
  justify-content: flex-start;
  align-items: center; */
}

#Home .home_text {
  width: 50%;
  height: 100%;
}

#Home .home_text h2 {
  font-size: 5rem;
  display: block;
}

#Home .home_text p {
  margin-top: 12px;
  line-height: 77px;
}

#Home .home_text p span {
  font-size: 2.5rem;
  color: #222;
  background: #fff;
  padding: 3px 50px;
  font-weight: bold;
}

/* ./components/menu/Resume */
#resume {
  width: 100%;
  height: 100%;
}

#resume * {
  color: #222;
}

#resume .inner {
  width: 90%;
  height: 70vh;
  margin: 0 auto;
  position: relative;
}

#resume .inner .resume_wrap {
  position: absolute;
  top: calc(50% - 27.5vh);
  width: 100%;
  height: 100%;
  background: #fff;
  border-radius: 150px 20% 150px 15%;
  display: flex;
  justify-content: center;
  align-items: center;
}

#resume .inner .resume_wrap .main {
  width: 97.5%;
  height: 95%;
  border: 5px solid black;
  border-style: dashed;
  border-radius: 150px 20% 150px 15%;
  position: relative;
  display: flex;
}

#resume .inner .resume_wrap .main > div {
  padding-top: 25px;
}

#resume .main hr {
  border: 2px solid;
}

#resume .main p,
#resume .main li {
  font-size: 22px;
  font-weight: normal;
}

#resume .main .L_main {
  width: 35%;
  height: 100%;
}

#resume .main .L_main figure {
  width: 100%;
  height: 50%;
  padding-left: 100px;
}

#resume .main .L_main figure img {
  width: 300px;
  height: 100%;
  border-radius: 50%;
  object-fit: cover;
}

#resume .main .L_main div {
  padding-left: 50px;
  margin-top: 1.5rem;
  line-height: 50px;
}

#resume .main .M_main {
  width: 15%;
  height: 100%;
}

#resume .main .R_main {
  width: 50%;
  height: 100%;
}

#resume .main .R_main div {
  width: 70%;
  margin-left: 50px;
  margin-bottom: 50px;
}

#resume .main .R_main div ul li {
  list-style: circle;
}

#resume .main .R_main div:last-child ul {
  margin-top: 1rem;
}

#resume .main .R_main div:last-child ul li {
  list-style: none;
  width: 50px;
  height: 50px;
  float: left;
}

/* ./components/menu/projects (swiper)*/
#projects {
  width: 100%;
  height: 75vh;
}

#projects .swiper {
  width: 100%;
  padding-top: 50px;
  padding-bottom: 50px;
}
#projects .swiper-slide {
  background-position: center;
  background-size: cover;
  width: 1200px;
  height: 650px;
}

#projects .swiper-slide .slide_wrap {
  width: 100%;
  height: 100%;
  background-color: #fff;
  border-radius: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}

#projects .swiper-slide .contents {
  width: 95%;
  height: 95%;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #222;
  border-radius: 50px;
  border-style: dashed;
}
#projects .swiper-slide .contents_wrap {
  width: 50%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
#projects .swiper-slide .contents_wrap:nth-child(1) img{
  border:5px solid rgba(0, 0, 0,0.5);
  border-style: double;
  object-fit: contain;
}
#projects .swiper-slide .contents_wrap:nth-child(2) {
  flex-direction: column;
  align-items: center;
  color: #222;
}

#projects .swiper-slide .contents_text {
  width: 100%;
  height: 22.5%;
}
#projects h4{
  margin-bottom:1rem;
}

#projects .contents_text ul li{
  float:left;
  margin: 8px 15px 0 0;
}
#projects .contents_text ul li img {
  width: 50px;
  height: 50px;
}
#projects .swiper-slide-active {
  transform: scale(1);
  opacity: 1;
}

#projects .swiper-slide img {
  display: block;
  width: 70%;
  height: 90%;
  object-fit: cover;
}

/* ./components/menu/item/skills */
#skills{
  width:100%;
  height:100%;
}
#skills *{
  color:#222;
}
#skills .inner{
  max-width:1300px;
  height:75vh;
  background: #fff;
  margin-top:5vh;
  position: relative;
  display: flex;
}

#skills .left{
  width:30%;
  height:100%;
  border-right:1px solid #222;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
#skills .left img{
  width: 125px;
  height: 150px;
  border-radius:50%;
  margin-bottom:50px;
}
#skills .left>div{
  width: 275px;
  display: inline-block;
}
#skills .left ul:first-child{
  width: 35%;
  display: inline-block;
}

#skills .left ul:last-child{
  width: 65%;
  display: inline-block;
}
#skills .left ul:last-child li{
  border-left:1px dotted #222;
}
#skills .left li{
  text-align: center;
  font-size: 1rem;
  margin-bottom:0.5rem;
}

#skills .right{
  width:70%;
  height:100%;
  overflow: scroll;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
}
#skills .right .skill_card{
  width:40%;
  height:150px;
  border:1px solid grey;
  display: flex;
  justify-content: space-between;
  border-radius: 5px;
  margin-bottom:50px;
}

#skills .right .skill_img{
  width:25%;
  height:100%;
  border-right:1px solid #222;
  text-align: center;
}

#skills .right .skill_img img{
  width: 60px;
  height: 100%;
}
#skills .right .skill_star{
  width:70%;
  height:100%;
}
#skills .right .skill_star h2{
  margin: 20px 0;
}
#skills .right .skill_star svg{
  width:45px;
  height:40px;
}
/* ./components/menu/Contact */
#contact {
  width: 100%;
  height: 100%;
}

#contact * {
  color: #222;
}

#contact .card_wrap {
  width: 600px;
  height: 600px;
  background: #fff;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50px;
  margin-top: 5%;
}

#contact .card_wrap .card {
  width: 95%;
  height: 95%;
  border: 2px dashed;
  border-radius: 50px;
  position: relative;
}

#contact .card > div {
  width: 100%;
  height: 100%;
  text-align: center;
  line-height: 70px;
}

#contact .card > div h2 {
  width: 200px;
  margin: 0 auto;
  border-bottom: 2px solid #222;
  margin-bottom: 50px;
}

#contact .card h3{
  height:60px;
}

#contact .card > div li {
  cursor: pointer;
  margin-bottom:2rem;
}
#contact .card > div li svg{
  width:50px;
  height:50px;
}
/* ./components/Footer */
#footer * {
  cursor: default;
}

#footer {
  width: 100%;
  height: 50px;
  position: absolute;
  bottom: 0;
  left: 0;
  border-top: 1px solid rgba(100, 100, 100, 0.5);
}

/* #footer > span {
  padding-left: 2rem;
} */

#footer .inner{
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
#footer ul{
  width:100%;
  position: absolute;
  bottom:0;
  transform: translateX(calc(50% - 15px));
}

#footer ul li {
  cursor: pointer;
  width: 200px;
}

#footer ul li svg {
  cursor: pointer;
  font-size: 2rem;
}

/* #footer p {
  padding-right: 2rem;
} */

#footer p span {
  animation: colorful 2s linear infinite;
}
